@import url(https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
.App-bg {
  display: flex;
  min-width: 400px;
  max-width: 500px;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  /* position: fixed; */
  top: 0;
  bottom: 0;
  width: 100%;
  min-height: 720px;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: nowrap;
}

.main-img {
  margin-top: 30px;
  margin-bottom: 1ex;
  max-width: 300px;
}

.main-test {
  display: inline-block;
  margin: 0;
  width: 10ex;
  height: 2.5ex;
  line-height: 2.5ex;
  border-radius: 2ex;
  box-sizing: border-box;
  border-style: solid;
  border-width: 1px;
  border-color: black;
  font-size: 25px;
}

.main-text {
  margin: -1ex;
  font-size: 60px;
}

.start-btn {
  font-family: 'Nanum Pen Script', cursive;
  font-size: 38px;
  transition: all .5s ease-in-out;
  border-radius: 35px;
  box-shadow: 0 1px 7px rgba(0,0,0,0.25);
  border: 0;
  width: 70px;
  height: 70px;
  margin-top: 30px;
  color: white;
  background-color: black;
}

.start-btn:hover {
  color: black;
  background-color: white;
}
.question__container {
  /* box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 5px;
  background-color: white; */
  padding: 50px;
  padding-top: 35px;
  margin: 0;
  margin-top: 50px;
  width: 100%;
  min-width: 400px;
  max-width: 500px;
  font-weight: bold;
  font-family: 'Noto Sans KR', sans-serif;
}

.progress-bar__container {
  position: relative;
  margin-bottom: 50px;
}

.progress-bar {
  border-radius: 60px;
  overflow: hidden;
  width: 100%;
}

.bar {
  background: rgba(0, 0, 0, 0.1);
}

.progress {
  transition: width 1.5s;
  background: tomato;
  color: #fff;
  padding: 5px;
  width: 0;
}

.progress-bar span {
  display: block;
}

.progress-bar {
  left: 50%;
  max-width: 90%;
  position: absolute;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.question__q {
  position: relative;
  height: 120px;
  margin-top: 100px;
  margin-bottom: 100px;
  font-size: 25px;
  display: flex;
  flex-direction: column;
  text-align: left;
  line-height: 1.2;
}

.question__btn {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  transition: all .5s ease-in-out;
  box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  width: 300px; 
  height: 90px;
  margin-bottom: 20px;
  background-color: rgba(255, 72, 0, 0.25);
  border-color: rgba(255, 72, 0, 0.5);
  border-style: dotted;
  color: darkslategrey;
  outline: 0;
}

.question__btn:hover {
  box-shadow: 0px 1px 7px rgba(0, 0, 0, 0.7);
  color: black;
}
.result__container {
  font-family: 'Noto Sans KR', sans-serif;
}

.loader {
  width: 100%;
  line-height: 700px;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.App-body {
  background-image: none;
}
* {
    box-sizing: border-box;
}
  
body {
    margin: 0;
    padding: 0;
    font-family: 'Nanum Pen Script', cursive
        /*, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif*/;
    background-color: #eff3f7;
    height: 100%;
    line-height: 1.8;
}

.App {
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    justify-content: center;
}

.App-header {
    display: inline-block;
    width: 400px;
}

.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.App-body {
    display: flex;
    min-width: 400px;
    max-width: 500px;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    /* position: fixed; */
    top: 0;
    bottom: 0;
    width: 100%;
    min-height: 720px;
    margin-left: auto;
    margin-right: auto;
    flex-wrap: nowrap;
}
